
body {
    width: 100%;
    height: 100%;
    margin: 0;
}
.slideout-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 256px;
    height: 100vh;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    z-index: 0;
    display: none;
    background-color: #1D1F20;
    color: white;
}
.slideout-menu-left {
    left: 0;
}
.slideout-menu-right {
    right: 0;
}
.slideout-open,
  .slideout-open body,
  .slideout-open .slideout-panel {
    overflow: hidden;
    transition: all 0.5s ease;
}
.slideout-open .slideout-menu {
    display: block;
    transition: all 0.5s ease;
}
.btn-primary:active {
    background-color: #635B65;
    box-shadow: none;
}
.btn-primary:active:focus {
  color: #ffffff; 
  background-color: #635B65; 
  border-color: #635B65;
}



